import { Link } from 'react-router-dom'
import Logo from '../../assets/images/icons/qiwi-logo.svg'
import './HeaderExtra.scss'
import ProfilePhoto from '../../assets/images/profile.png'
import ExtraHeader from '../ExtraHeader'

const LargeHeadaer = (props) => {

    const { token } = props

    return (
        <header className="header-extra">
            <div className="container">
                <div className="header-top">
                    <div className="header-left">
                        <Link to='/'>
                            <img src={Logo} alt="Logo" />
                        </Link>
                        <p>Продай, найди, купи <br />
                    все что пожелаешь…</p>
                    </div>
                    <div className="header-center">
                        <ul>
                            <li><a href="#">Объявления</a></li>
                            <li><a href="#">Магазины</a></li>
                            <li><a href="#">Для бизнеса</a></li>
                            <li><a href="#">Помощь</a></li>
                        </ul>
                    </div>
                    <div className="header-right">
                        <ul className='language'>
                            <li><a href="#" className="active-language">Рус</a></li>
                            <li><a href="#">O’z</a></li>
                        </ul>
                        {
                            token ? (
                                <Link  to='/userpost' className="profile">
                                    <p>Темиров</p>
                                    <img src={ProfilePhoto} alt="" />
                                </Link>
                            ) : (
                                <ul className='registration'>
                                    <li><Link to='/login'>Вход</Link></li>
                                    <li><Link to='/registration-1' className='active'>Регистрация</Link></li>
                                </ul>
                            )
                        }
                    </div>
                </div>
                <div className="header-bottom">
                    <div className="header-bottom-left">
                        <form className="search">
                            <select>
                                <option selected disabled>Любая категория</option>
                                <option>Автомобили</option>
                                <option>Недвижимость</option>
                                <option>Электроника</option>
                                <option>Работа</option>
                            </select>
                            <input type="search" placeholder="Что будем искать ?" />
                            <select>
                                <option selected disabled>По всей Узбекистану</option>
                                <option>Ташкент</option>
                                <option>Фергана</option>
                                <option>Наманган</option>
                            </select>
                            <button type="submit">Найти</button>
                        </form>
                    </div>
                    <div className="header-bottom-right">
                        <button className="adding-product" >
                            Добавить обьявления
                        </button>
                    </div>
                </div>
                <ExtraHeader />
            </div>
        </header>
    )
}

export default LargeHeadaer