import { lazy, useState, Suspense, useEffect } from "react"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom"
import './assets/main.scss'
import Header from './containers/Header'
import Logo from './assets/images/icons/qiwi-logo.svg'
import { Helmet } from 'react-helmet'
import LargeHeadaer from './containers/HeaderWithExtra'

const LoginPage = lazy(() => import('./pages/LoginPage'));
const RegisterFirstStage = lazy(() => import('./pages/Register-1'));
const RegisterSecondStage = lazy(() => import('./pages/Register-2'));
const ErrorPage = lazy(() => import('./pages/ErrorPage'));
const HomePage = lazy(() => import('./pages/HomePage'));
const ChattingPage = lazy(() => import('./pages/ChattingPage'));
const SinglePostPage = lazy(() => import('./pages/SinglePostPage'));
const UserPost = lazy(() => import('./pages/UserPost'));

const App = () => {

  const [token, setToken] = useState(window.localStorage.getItem('sessionToken'));
  const [pathname, setPathname] = useState(window.location.pathname)
  return (
    <Router>
      <Helmet>
        <link rel="icon" href={Logo} />
      </Helmet>
      {
        pathname === '/chatting' || pathname === '/userpost' ? (
          <LargeHeadaer token={token} />
        ) : (
          <Header token={token} />
        )
      }
      <Suspense fallback={<h1>Loading....</h1>}>
        <div>
          {
            token ? (
              <Switch>
                <Route path="/" exact component={HomePage} />
                <Redirect from='/home' to='/' />
                <Route path='/singlepost' component={SinglePostPage} />
                <Route path='/userpost' component={UserPost} />
                <Route path='/chatting' component={ChattingPage} />
                <Route path="*" component={ErrorPage} />
              </Switch>
            ) : (
              <Switch>
                <Route path="/" exact component={HomePage} />
                <Redirect from='/home' to='/' />
                <Route path='/login' component={LoginPage} setToken={setToken} />
                <Route path='/singlepost' component={SinglePostPage} />
                <Route path='/registration-1' component={RegisterFirstStage} />
                <Route path='/registration-2' component={RegisterSecondStage} />
                <Route path="*" component={ErrorPage} />
              </Switch>
            )
          }

        </div>
      </Suspense>

    </Router>
  );
}

export default App;
