import { useState } from 'react'
import './ExtraHeader.scss'

const ExtraHeader = () => {
    const [buttonStatus, setButtonStatus] = useState('new')

    return (
        <>
            <div className="extra-header-wrapper">
                <div className="top-extra-header">
                    <div className="left-header">
                        <h3>Здравствуйте Зухриддин Темиров</h3>
                    </div>
                    <div className="right-header">
                        <span className='account'>Ваш счет: 2 3000 сум</span>
                        <button className='adding'>
                            Пополнить
                    </button>
                    </div>
                </div>
                <div className="bottom-extra-header">
                    <div className="top-button-group">
                        <button onClick={() => setButtonStatus('new')} className={`status-header-button ${buttonStatus === 'new' ? 'active' : ' '}`}>
                            Объявления
                    </button>
                        <button onClick={() => setButtonStatus('message')} className={`status-header-button ${buttonStatus === 'message' ? 'active' : ' '}`}>
                            Сообщения</button>
                        <button onClick={() => setButtonStatus('payment')} className={`status-header-button ${buttonStatus === 'payment' ? 'active' : ' '}`}>
                            Платежи и счёт
                    </button>
                        <button onClick={() => setButtonStatus('settings')} className={`status-header-button ${buttonStatus === 'settings' ? 'active' : ' '}`}>
                            Настройки
                    </button>
                        <button onClick={() => setButtonStatus('bussines')} className={`status-header-button ${buttonStatus === 'bussines' ? 'active' : ' '}`}>
                            Мой бизнес
                    </button>
                    </div>
                    <div className="bottom-line">
                        <div className="line"></div>
                        <span className={`active-line ${buttonStatus}`}></span>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ExtraHeader